    <br /><br />
    ROC curves were first employed during World War 2 to analyze radar signals: After
    missing the Japanese aircraft that carried out the attack on Pearl Harbor, the
    US wanted their radar receiver operators to better identify aircraft from signal
    noise. The operator's ability to identify as many true positives as possible
    while minimizing false positives was named the
    <i>Receiver Operating Characteristic</i>, and the curve analyzing their
    predictive abilities was called the ROC Curve.

    <br /><br />
    Today, ROC curves are used in a number of contexts. In machine learning, we use
    them to analyze the predictive power of a clasifier. ROC Curves provide a visual
    way to observe how changes in our model’s classification thresholds affect our
    model’s performance. Just as in the past, they help us identify the classification
    threshold that best identifies true positives while minimizing false positives.
    <br /><br />
    An ROC curve is composed by plotting the
    <span class="bold">True-Positive Rate (TPR)</span>
    versus the <span class="bold">False-Positive Rate (FPR)</span> for different
    classification threshold, where
    <br />
  </p>
  <ul class="body-text">
    <li>
      <span class="bold">True Positive Rate (TPR)</span>: The probability that a
      positive sample is predicted in the positive class:<br /><br />
      {@html katexify(
        `\\begin{aligned}\\mathrm{TPR} = \\frac{\\mathrm{\\#~True~Positives}}{\\mathrm{\\#~True~Positives + \\#~False~Negatives}} \\end{aligned}`
      )} <br /> and
    </li>

    <li>
      <span class="bold">False Positive Rate (FPR)</span>: The probability that
      a negative sample is predicted in the positive class:<br /><br />
      {@html katexify(
        `\\begin{aligned}\\mathrm{FPR} = \\frac{\\mathrm{\\#~False~Positives}}{\\mathrm{\\#~False~Positives + \\#~True~Negatives}} \\end{aligned}`
      )}
    </li>
  </ul>
  <br />
  <p class="body-text">
    <!-- ROC curves are used in clinical biochemistry to choose the most appropriate
    cut-off for a test. The best cut-off has the highest true positive rate
    together with the lowest false positive rate. 
     -->
