<script>
  import Scrolly from "./Scrolly.svelte";
  import katexify from "../katexify";
  import { select, selectAll } from "d3-selection";
  // import BeeSwarm from "./Beeswarm.svelte";
  import ROCScatter from "./ROCScatter.svelte";
  // import ConfusionMatrix from "./ConfusionMatrix.svelte";
  // import { TP, FP, TN, FN, stage } from "./data-store.js";
  import {
    TP,
    FP,
    TN,
    FN,
    TPR,
    FPR,
    rocCircles,
    xPoss,
  } from "../data-store.js";
  import { LayerCake, Svg, Html } from "layercake";
  import { range } from "d3-array";
  import { scaleLinear, scaleOrdinal } from "d3-scale";
  import Key from "./Key-html.svelte";
  import AxisX from "./BeeswarmAxisX.svelte";
  import AxisY from "./AxisY.svelte";
  import Beeswarm from "./BeeswarmForce.svelte";
  import DecisionBoundary from "./DecisionBoundary.svelte";
  import { scatterData } from "../datasets.js";
  import { transition } from "d3-transition";

  const xKey = "xVal";
  const zKey = "yVal";
  const r = 6.5;
  const seriesNames = new Set();
  const seriesColors = ["#c9208a", "#7cd1ea"];
  const reScaleX = scaleLinear().domain([0.4, 15]).range([0, 1]);
  const dataTransformed = scatterData.map((d) => {
    seriesNames.add(d[zKey]);
    return {
      [zKey]: d[zKey],
      [xKey]: +d[xKey],
      xx: +d[xKey],
    };
  });

  // scroll iterator
  let value;
  // variable to break loop if scroll up too fast
  let scrollStageTrack = 2;
  let t1;
  let t2;

  // Paragraph text for scrolly
  $: steps = [
    `<h1 class='step-title'>Classification Threshold</h1>
   
       
       <p>Say we begin with our model's classification threshold all the way to the left at zero. So anything with a probability greater than zero of being class 1, we'll classify as class 1. 
       Of course this model is awful - it just predicts everything to be 1. While this will correctly classify every positive data point, yielding a perfect TPR=1, 
       it comes at the cost of mis-labeling every negative data point, yielding a perfectly awful FPR=1. 
      </p>`,
    `<h1 class='step-title'>Some New Thresholds</h1>
      <p>Of course, we can do better. But how much better? Well, to find out, let's increase our threshold.
        As we move our threshold more and more to the right, we'll track the corresponding TPR and FPR at each threshold we try, so we can easily compare
        them.
    `,
    `<h1 class='step-title'>And More Thresholds</h1>
      <p>We want to find the classification threshold that best maximizes true positives while minimizng false positives. 
        <br><br>
        To find that threshold, we'll sample all remaining possible values for our threshold.</p>`,
    `<h1 class='step-title'>The R.O.C. Curve</h1>
      <p>After trying all classification thresholds and plotting their corresponding TPR versus it's FPR, we obtain our ROC curve.
        <br><br>
        <span class='bold'>Drag the threshold for yourself to see its corresponding point along our ROC Curve!</span>
      </p>`,
  ];

  const target2event = {
    0: () => {
      $rocCircles = [];
      $xPoss = -0.01;
    },
    1: () => {
      // clearTimeout(t2);
      scrollStageTrack = 1;
      const xs = range(0.0, 0.45, 0.015);
      xs.forEach((x, i) => {
        t1 = setTimeout(() => {
          if (scrollStageTrack === 1) {
            $xPoss = x;
          }
        }, i * 40);
      });
    },

    2: () => {
      // clearTimeout(t1);

      scrollStageTrack = 2;
      selectAll(".roc-circle").transition().attr("r", 5.5);
      const xs = range(0.45, 0.98, 0.015);
      xs.forEach((x, i) => {
        t2 = setTimeout(() => {
          if (scrollStageTrack === 2) {
            $xPoss = x;
          }
        }, i * 40);
      });

      select(".highlight-circle").style("opacity", 1);
      select("#highlight-text").style("opacity", 1);
      select("#highlight-tspan").style("opacity", 1);
    },

    3: () => {
      selectAll(".roc-circle")
        .transition()
        .attr("r", 6)
        .transition()
        .attr("r", 0);

      select(".highlight-circle").style("opacity", 0);
      select("#highlight-text").style("opacity", 0);
      select("#highlight-tspan").style("opacity", 0);
    },
    4: () => {},
  };

  // trigger events on scroll typeof lastname !== "undefined"
  $: if (typeof value !== "undefined") target2event[value]();
</script>

<!-- <h2 class="body-header">Radio Operator Curves (ROC)</h2>
<p class="body-text">
  Here's an example of a typical side-scroller. It's responsive, and will fold
  to an overlap scroll if the screen gets small enough:
</p> -->
<section>
  <!-- scroll container -->
  <div class="section-container">
    <div class="steps-container">
      <Scrolly bind:value>
        {#each steps as text, i}
          <div class="step" class:active={value === i}>
            <div class="step-content">{@html text}</div>
          </div>
        {/each}
        <div class="spacer" />
      </Scrolly>
    </div>
    <div class="charts-container">
      <div class="bs-chart">
        <LayerCake
          padding={{ bottom: 15 }}
          x={xKey}
          y="50"
          z={zKey}
          zScale={scaleOrdinal()}
          zDomain={Array.from(seriesNames)}
          zRange={seriesColors}
          data={dataTransformed}
          let:width
        >
          <Svg>
            <AxisX />
            <!-- <AxisY /> -->
            <Beeswarm
              r={width < 400 ? r / 1.15 : r}
              strokeWidth={1}
              xStrength={0.05}
              yStrength={0.075}
            />
            <DecisionBoundary />
          </Svg>

          <Html pointerEvents={false}>
            <Key shape="circle" />
          </Html>
        </LayerCake>
      </div>
      <!-- <BeeSwarm /> -->
      <ROCScatter />
    </div>
  </div>
  <!-- end scroll -->
  <br /><br />
  <p class="body-text">And that's the end of our scrolly.</p>
</section>

<style>
  :global(body) {
    overflow-x: hidden;
  }

  :global(h1) {
    color: var(--squid-ink);
    font-family: var(--font-heavy);
  }

  :global(p) {
    color: var(--squid-ink);
    font-family: var(--font-main);
    font-weight: none;
  }

  .bs-chart {
    /* margin: auto; */
    width: 90%;
    max-height: 95%;
    padding-left: 10%;
    /* max-height: 50%; */
    /* border: 3px solid skyblue; */
  }

  /* space after scroll is finished */
  .spacer {
    height: 40vh;
  }

  .charts-container {
    position: sticky;
    top: 10%;
    display: grid;
    width: 50%;
    grid-template-columns: 100%;
    grid-row-gap: 3rem;
    grid-column-gap: 0rem;
    grid-template-rows: 45% 45%;
    height: 80vh;
    padding-right: 1rem;
    padding-left: 8%;
    /* margin: auto; */
    /* border: 3px solid black; */
  }

  .section-container {
    margin-top: 1em;
    text-align: center;
    transition: background 100ms;
    display: flex;
    flex-direction: row-reverse;
  }

  .step {
    height: 110vh;
    display: flex;
    place-items: center;
    justify-content: center;
  }

  .step-content {
    font-size: 18px;
    background: whitesmoke;
    color: #ccc;
    border-radius: 1px;
    padding: 0.5rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: background 500ms ease;
    text-align: left;
    width: 75%;
    margin: auto;
    max-width: 500px;
    font-family: var(--font-main);
    line-height: 1.3;
    border: 5px solid #232f3e;
  }

  .step.active .step-content {
    background: #f1f3f3bd;
    color: var(--squid-ink);
  }

  .steps-container {
    height: 100%;
  }

  .steps-container {
    flex: 1 1 40%;
    z-index: 10;
  }

  /* Comment out the following line to always make it 'text-on-top' */
  @media screen and (max-width: 950px) {
    .section-container {
      flex-direction: column-reverse;
    }

    .steps-container {
      pointer-events: none;
    }

    .charts-container {
      top: 7.5%;
      width: 95%;
      margin: auto;
    }

    .step {
      height: 145vh;
    }

    .step-content {
      width: 95%;
      max-width: 768px;
      font-size: 17px;
      line-height: 1.6;
    }

    .spacer {
      height: 100vh;
    }
  }
</style>
